.about-details {
    margin: 4rem 6rem;
    padding: 2rem; /* Add padding for better spacing */
    background-color: #f9f9f9; /* Light background for contrast */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  }
  
  .about-details h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
    color: #010101; /* Accent color */
    text-align: center; /* Center the main heading */
  }
  
  .about-details h2 {
    font-size: 2rem;
    margin-top: 2rem;
    color: #264653; /* Darker accent color for subheadings */
  }
  
  .about-details p {
    font-size: 1.1rem;
    line-height: 1.6;
    margin-bottom: 1.5rem; /* More space between paragraphs */
  }
  
  .about-details ul {
    list-style-type: none; /* Remove default bullets */
    padding-left: 0; /* Remove padding */
    margin: 1.5rem 0; /* Add margin for spacing */
  }
  
  .about-details li {
    background-color: #87CEEB; /* Light background for list items */
    border-radius: 5px; /* Rounded corners for list items */
    margin: 0.5rem 0; /* Space between list items */
    padding: 0.8rem 1.2rem; /* Padding for list items */
    font-size: 1.1rem;
    transition: background-color 0.3s; /* Smooth hover effect */
  }
  
  .about-details li:hover {
    background-color: #4169E1; /* Change color on hover */
    color: #fff; /* Change text color on hover */
  }
  