html {
  font-size: 16px; /* Base font size */
}

.sign-up-container {
  max-width: 25rem; /* 400px */
  margin: auto; /* Centering the container */
  padding: 1.25rem; /* 20px */
  border: 0.0625rem solid #ccc; /* 1px */
  border-radius: 0.5rem; /* 8px */
  box-shadow: 0 0.125rem 0.625rem rgba(0, 0, 0, 0.1); /* 0 2px 10px */
  display: flex; /* Use flexbox for alignment */
  flex-direction: column; /* Align items vertically */
}

.form-group {
  margin-bottom: 0.9375rem; /* 15px */
}

label {
  display: block;
  margin-bottom: 0.3125rem; /* 5px */
}

input {
  height: 3rem; /* Same height as in Contact Form */
  padding: 0 1rem; /* Horizontal padding */
  margin-bottom: 2rem; /* Bottom margin */
  border-radius: 0.3rem; /* 4px equivalent */
  border: 1px solid #2a2a2a; /* Consistent border color */
}

button {
  width: 100%;
  padding: 0.625rem; /* 10px */
  background-color: #007bff; /* Button background color */
  color: white;
  border: none;
  border-radius: 0.25rem; /* 4px */
  cursor: pointer;
  margin-top: 0.5rem; /* Add some space above the button */
}

/* Toggle Button Style */
.toggle-button {
  background: none;
  border: none;
  color: #007bff; /* Same color as the button */
  cursor: pointer;
  text-align: center;
  margin-top: 0.625rem; /* 10px */
}

/* Responsive Styles */
@media (max-width: 850px) {
  .sign-up-container {
    margin: 6rem 4rem; /* Reduced margin on smaller screens */
    padding: 1rem; /* Adjust padding for smaller screens */
  }

  .form-group {
    margin-bottom: 0.75rem; /* Reduced margin for smaller screens */
  }

  label {
    font-size: 1rem; /* Adjust label font size */
  }

  input {
    padding: 0 1rem; /* Keep horizontal padding consistent */
  }

  button {
    display: inline-block;
    padding: 0.625rem; /* Ensure padding remains consistent */
    margin-top: 1rem; /* Ensure some space above button */
  }

  .toggle-button {
    display: inline-block; /* Ensure it stays visible */
    margin-top: 1rem; /* Space above toggle button */
  }
}

/* Additional Media Query for Very Small Screens */
@media (max-width: 480px) {
  .sign-up-container {
    padding: 0.5rem; /* Further reduced padding */
  }

  button {
    padding: 0.5rem; /* Adjust padding for buttons */
    margin-top: 1rem; /* Ensure space above button */
  }

  .toggle-button {
    margin-top: 0.25rem; /* Further reduced margin */
  }
}
